import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { FacilityService } from 'src/app/api/controllers/Facility';
import { PropertyManagerService } from 'src/app/api/controllers/PropertyManager';
import { AssetService } from 'src/app/api/controllers/Asset';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';

import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
  selector: 'app-add-property-dialog',
  templateUrl: './add-property-dialog.component.html',
  styleUrls: ['./add-property-dialog.component.scss']
})
export class AddPropertyDialogComponent implements OnInit {

  public propertyTypeList: Array<any> = [];
  public unitList: Array<any> = [];
  public facilityList: Array<any> = [];
  public assetCategoryList: Array<any> = [];
  public assetSubCategoryList: Array<any> = [];
  public assetList: Array<any> = [];
  public assetRawList: Array<any> = [];
  public assetCategory: any;
  public assetSubCategory: any;
  public isEdit: boolean = false;
  public addPropertyParam = {
    "propertyPoiid": 0,
    "propertyPoitype": 0,
    "nodeId": 0,
    "x": 0,
    "y": 0,
    "id": 6,
    "blockUnitId": 0,
    "assetId": null,
    "facilityId": null,
    "infrastructureId": null,
    "opMaintenanceId": null,
    "apartmentId": this.sessionService.apartmentId,
    "isActive": true,
    "insertedBy": this.sessionService.userId,
    "insertedOn": "2020-07-06T14:48:10.120Z",
    "updatedBy": this.sessionService.userId,
    "updatedOn": "2020-07-06T14:48:10.120Z",
    "description":""
  }
  constructor(
    private lookupService: LookupService,
    private apartmentService: ApartmentService,
    private facilityService: FacilityService,
    private sessionService: SessionService,
    private propertyService: PropertyManagerService,
    private assetService: AssetService,
    private sharedService : SharedService,
    public dialogRef: MatDialogRef<AddPropertyDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
    this.addPropertyParam.nodeId = this.data.nodeId;
    this.addPropertyParam.id = this.data.radius;
    // this.addPropertyParam.x = this.data.x;
    // this.addPropertyParam.y = this.data.y;
    if (this.data.type == 'add') {
      this.isEdit = false;
    } else {
      this.isEdit = true;
      this.addPropertyParam.propertyPoiid = Number(this.data.poiId);
      this.getProperty();
    }
    // for (const key in this.data) {
    //   if(this.addPropertyParam.hasOwnProperty(key)){
    //     this.addPropertyParam[key] = this.data[key];
    //   }
    // }
  }

  ngOnInit(): void {
    this.getPropertyType();
  }

  getProperty() {
    let queryParamBase = {
      apartmentId: this.sessionService.apartmentId,
      propertyPOIID: this.addPropertyParam.propertyPoiid,
    };

    this.propertyService.getPropertyPoiUnitById(queryParamBase).subscribe(
      (resp: any) => {
        if (resp[0]) {
          for (const key in resp[0]) {
            if (this.addPropertyParam.hasOwnProperty(key)) {
              this.addPropertyParam[key] = resp[0][key];
            }
          }
          this.selectPropertyType();
        }
      }
    )
  }


  getPropertyType() {
    let getLookUpParam = {
      ApartmentId: this.sessionService.apartmentId,
      LookupTypeId: 196
    }
    this.lookupService.getLookupValueByLookupTypeId(getLookUpParam).subscribe(
      (resp: any) => {
        this.propertyTypeList = resp;
      },
      error => {

      }
    )
  }

  selectPropertyType() {
    switch (Number(this.addPropertyParam.propertyPoitype)) {
      case 316:

        break;
      case 318:
        this.getUnitList();
        break;
      case 319:
        this.getAssetCategory();
        this.getAsset();
        break;
      case 320:
        this.getFacilityList();
        break;
      case 321:

        break;
      case 322:

        break;


      default:
        break;
    }
  }

  getUnitList() {
    let getUnitParam = {
      apartmentBlockId: this.data.apartmentBlockId
    }
    this.apartmentService.getApartmentBlockUnitByBlockId(getUnitParam).subscribe(
      (resp: any) => {
        this.unitList = resp;
      }
    )
  }

  getFacilityList() {
    let getFacilityParam = {
      apartmentId: this.sessionService.apartmentId
    }
    this.facilityService.getApartmentFacilitiesByApartmentId(getFacilityParam).subscribe(
      (resp: any) => {
        this.facilityList = resp;
      }
    )
  }

  getAssetCategory() {
    let queryParamBase = {
      apartmentId: this.sessionService.apartmentId,
      lookupTypeId: 19,
      subCategoryLookupTypeId: 68,
    };
    this.lookupService.getSubcategory(queryParamBase).subscribe((res: any) => {
      if (res) {
        this.assetCategoryList = res ? res : [];
      }
    });
  }

  getAssetSubCategory() {
    this.assetCategoryList.filter(key => {
      if (key.id == this.assetCategory) {
        this.assetSubCategoryList = key.subCategory;
      }
    })
  }

  getAsset() {
    let getAllAsset = {
      apartmentId: this.sessionService.apartmentId
    }
    this.assetService.getAllAssetByApartmentId(getAllAsset).subscribe(
      (resp: any) => {
        this.assetRawList = resp;
      }
    )
  }

  selectAsset() {
    this.assetRawList.filter(key => {
      if (key.assetSubcategoryId == this.assetSubCategory) {
        this.assetList.push(key);
      }
    })
  }

  setPropertyType(id) {
    switch (this.addPropertyParam.propertyPoitype) {
      case 316:

        break;
      case 318:
        this.assignUnitData(id);
        break;
      case 319:

        break;
      case 320:
        break;
      case 321:

        break;
      case 322:

        break;


      default:
        break;
    }
  }

  assignUnitData(poiId) {
    let unitData: any = new Object();
    unitData['type'] = "318";
    unitData['poiId'] = poiId;
    unitData['radius'] = this.addPropertyParam.id;
    this.unitList.filter(key => {
      if (key.apartmentBlockUnitId == this.addPropertyParam.blockUnitId) {
        for (const item in key) {
          if (key[item] === true && key[item] != null) {
            unitData[item] = item;
          }
        }
      }
    })
    delete unitData.isActive;
    this.dialogRef.close(unitData);
  }

  addProperty() {
    this.addPropertyParam.insertedOn = _moment().toISOString();
    this.addPropertyParam.updatedOn = _moment().toISOString();
    this.addPropertyParam.id = Number(this.addPropertyParam.id);
    this.addPropertyParam.blockUnitId = Number(this.addPropertyParam.blockUnitId);
    this.addPropertyParam.propertyPoitype = Number(this.addPropertyParam.propertyPoitype);
    let addProperty = {
      propertyPoi: this.addPropertyParam
    }
    this.propertyService.addPropertyPoi(addProperty).subscribe(
      (resp: any) => {
        if (resp && resp.message) {
          this.sharedService.openSnackBar('Property Poi Added Successfully','success');
          this.setPropertyType(resp.message)
        }
      }
    )
  }

  updateProperty() {
    this.addPropertyParam.updatedOn = _moment().toISOString();
    let addProperty = {
      propertyPoi: this.addPropertyParam
    }
    this.propertyService.updatePropertyPoi(addProperty).subscribe(
      (resp: any) => {
        if (resp && resp.message) {
          this.sharedService.openSnackBar('Property Poi Updated Successfully','success');
          this.setPropertyType(resp.message)
        }
      }
    )
  }

}
